import React from 'react';
import ReactDOM from 'react-dom/client';
import "./index.css";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Home from "./Pages/Home";
import Why from "./Pages/Why";
import Navbar from "./Components/Navbar";

import "bootstrap/dist/css/bootstrap.min.css";
import { allArticles } from "./data";

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <Navbar />
    <BrowserRouter>
      <Routes>
        <Route path="/" exact element={<Home />} />
        <Route path="/why" exact element={<Why />} />
      </Routes>
    </BrowserRouter>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();

//
//
//

let populateHomeArticle = () => {
  let articles = allArticles;

  for (let i = 0; i < articles.length; i++) {
    if (i === articles.length - 1) {
      let currentArticle = articles[i];
      document.getElementById("currentArticle").insertAdjacentHTML(
        "beforeend",
        `<div id="homeContainer">
                <h2 id="homeTitle">${currentArticle.title}</h2> 
                <img id="homeImage" src="${currentArticle.image}" /> 
                <h5 id="homeDate">${currentArticle.date}</h5> 
                <p id="homeText">${currentArticle.text}</p> 
           </div>`
      );
    }
  }
};

populateHomeArticle();

//
//
//

let renderSelected = (articleId) => {
  let articles = allArticles;

  document.getElementById("currentArticle").innerHTML = "";

  for (let i = 0; i < articles.length; i++) {
    if (articles[i].id === articleId) {
      let currentArticle = articles[i];
      document.getElementById("currentArticle").insertAdjacentHTML(
        "beforeend",
        `<div id="homeContainer">
                <h2 id="homeTitle">${currentArticle.title}</h2>
                <img id="homeImage" src="${currentArticle.image}" />
                <h5 id="homeDate">${currentArticle.date}</h5>
                <p id="homeText">${currentArticle.text}</p>
           </div>`
      );
    }
  }
};

//
//
//
let populateGrid = () => {
  let articles = allArticles;

  for (let i = 0; i < articles.length; i++) {
    document.getElementById("gridContainer").insertAdjacentHTML(
      "beforeend",
      `<div class="gridBox" id="${articles[i].id}">
           <p class="gridTitle" >${articles[i].title}</p>
           <img class="gridImage" src="${articles[i].image}">
    
           </div>`
    );
  }

  for (
    let i = 1;
    i < document.getElementById("gridContainer").children.length;
    i++
  ) {
    let getRenderSelected = () => {
      renderSelected(document.getElementById("gridContainer").children[i].id);
    };

    document
      .getElementById("gridContainer")
      .children[i].addEventListener("click", getRenderSelected);
  }
};

populateGrid();

