import { Menu } from "@mui/icons-material";
import Nav from "react-bootstrap/Nav";
import { NavDropdown } from "react-bootstrap";
import Dropdown from "react-bootstrap/Dropdown";
import NavItem from "react-bootstrap/NavItem";
import NavLink from "react-bootstrap/NavLink";
import styled from "styled-components";
import { allArticles } from "../data";

let Container = styled.div`
  /* border: solid; */
  border-bottom: solid;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: #dcc48e;
  display: flex;
  justify-content: space-between;
  padding: 10px;
`;

let LogoContainer = styled.div`
  display: flex;
  gap: 10px;
  padding-top: 10px;
  padding-left: 10px;
`;

let Logo = styled.h2`
  font-family: "Bruno Ace", cursive;
  font-size: 24px;
  font-weight: 500;
`;

let HomeLogoName = styled.h3`
  font-family: "Rubik", sans-serif;
  font-size: 24px;
  font-weight: 500;
  text-decoration: none;
`;

let MyButton = styled.button`
  background-color: #dcc48e;
  border: none;
  color: black;
  display: flex;
  gap: 10px;
  width: 100%;
`;

const Navbar = () => {
  let renderWhy = () => {
    document.getElementById("currentArticle").innerHTML = "";

    let articles = allArticles;

    for (let i = 0; i < articles.length; i++) {
      if (articles[i].title === "Why?") {
        let currentArticle = articles[i];
        document.getElementById("currentArticle").insertAdjacentHTML(
          "beforeend",
          `<div id="homeContainer">
                  <h2 id="homeTitle">${currentArticle.title}</h2> 
                  <img id="homeImage" src="${currentArticle.image}" /> 
                  <h5 id="homeDate">${currentArticle.date}</h5> 
                  <p id="homeText">${currentArticle.text}</p> 
            </div>`
        );
      }
    }
  };

  let renderLatest = () => {
    document.getElementById("currentArticle").innerHTML = "";

    let articles = allArticles;

    for (let i = 0; i < articles.length; i++) {
      if (i === articles.length - 1) {
        let currentArticle = articles[i];
        document.getElementById("currentArticle").insertAdjacentHTML(
          "beforeend",
          `<div id="homeContainer">
                  <h2 id="homeTitle">${currentArticle.title}</h2> 
                  <img id="homeImage" src="${currentArticle.image}" /> 
                  <h5 id="homeDate">${currentArticle.date}</h5> 
                  <p id="homeText">${currentArticle.text}</p> 
            </div>`
        );
      }
    }
  };

  return (
    <Container>
      <LogoContainer>
        <MyButton type="button" onClick={renderLatest}>
          <HomeLogoName>Jovany</HomeLogoName>
          <Logo>READS</Logo>
        </MyButton>
      </LogoContainer>
      <Nav>
        <Dropdown as={NavItem}>
          <Dropdown.Toggle as={NavLink} style={{ color: "black" }}>
            <Menu />
          </Dropdown.Toggle>
          <Dropdown.Menu>
            <MyButton type="button" onClick={renderLatest}>
              <Dropdown.Item>Home</Dropdown.Item>
            </MyButton>
            <Dropdown.Item href="#otherBlogs" style={{ textAlign: "center" }}>
              All Blogs
            </Dropdown.Item>
            <NavDropdown.Divider />
            <MyButton type="button" onClick={renderWhy}>
              <Dropdown.Item id="whyButton">WHY?</Dropdown.Item>
            </MyButton>
          </Dropdown.Menu>
        </Dropdown>
      </Nav>
    </Container>
  );
};

export default Navbar;
