    image: "https://i.stack.imgur.com/zTh02m.jpg",
    date: "05/05/2023",
    text: "Why blog about books? I have a few reasons. First, I find that articulating my thoughts into coherent sentences requires a clear, if not deep, understanding of those thoughts. I seek a clearer understanding of my thoughts on the books I read and aim to refine the skill of expressing my thoughts clearly. It's like hitting two birds with one stone. These are not reviews. Instead, I would like to consider these entries as time capsules. Here, I will document what I thought or how I felt when I was in a particular phase of my life while reading these books. There are other less significant reasons as well, in no particular order: I want to remember more about what I read, and it would be fascinating to revisit my thoughts alongside the books I reread. I enjoy writing, and this provides a good excuse for it. In fact, I rarely get a chance to write, so it's therapeutic. Writing is akin to having a conversation with myself. The rest of the world fades away as I tap away at my keyboard. I sift through my thoughts, individually inspecting and dissecting each one. By the time I have a sentence I am satisfied with, my thoughts become clear. There is a sense of satisfaction that comes from arranging the right words in the right order, second only to the joy of finding those right words in the first place. In the end, it's as if a brief recording of my mind is reflected back at me, one that I can store for later, revisiting it as a new version of myself.",
